body{
  font-family: Arial, Helvetica, sans-serif;
  margin-left: auto;
  margin-right: auto;
  background-color: lightgray;
}
.center{
  line-height: 200px;
  height: 200px;
  border: 3px solid green;
  text-align: center;
}
#error{
  color: crimson;
}
#location{
  text-align: center;
  font-size: 24px;
}
#img{
  margin-left: auto;
  margin-right: auto;
}
#temp{
  font-weight: bold;
  font-size: 22px;
}
#horizontal{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
#head{
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#description{
  font-style: italic;
  font-size: 18px;
}
.input{
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}